import { useCallback, useState } from 'preact/hooks';

type FocusEvents = {
  onBlurWithin?: (e: FocusEvent) => void;
  onFocusWithin?: (e: FocusEvent) => void;
};

type UseFocusWithinProps = FocusEvents & {
  isDisabled?: boolean;
};

export function useFocusWithin({
  isDisabled,
  onBlurWithin,
  onFocusWithin
}: UseFocusWithinProps = {}) {
  const [isFocused, setIsFocused] = useState(false);
  const onfocusin = useCallback(
    (event: FocusEvent) => {
      if (!isFocused) {
        onFocusWithin?.(event);
        setIsFocused(true);
      }
    },
    [onFocusWithin, isFocused]
  );
  const onfocusout = useCallback(
    (event: FocusEvent) => {
      // Trigger focus event changes only when the focus goes outside of the current
      // target. Ignore focus changes within the current target
      if (
        isFocused &&
        (event.relatedTarget == null ||
          !(event.currentTarget as HTMLElement).contains(event.relatedTarget as HTMLElement))
      ) {
        onBlurWithin?.(event);
        setIsFocused(false);
      }
    },
    [onBlurWithin, isFocused]
  );

  return isDisabled
    ? {
        isFocused: false,
        focusProps: {}
      }
    : {
        isFocused,
        focusProps: {
          onfocusin,
          onfocusout
        }
      };
}
